html {
    overflow-x: hidden;
}
/* josefin-sans-regular - latin */
@font-face {
    font-family: "Josefin Sans";
    font-style: normal;
    font-weight: 400;
    src: url("./fonts/josefin-sans-v17-latin-regular.eot"); /* IE9 Compat Modes */
    src: local(""),
        url("./fonts/josefin-sans-v17-latin-regular.eot?#iefix") format("embedded-opentype"),
        /* IE6-IE8 */ url("./fonts/josefin-sans-v17-latin-regular.woff2") format("woff2"),
        /* Super Modern Browsers */ url("./fonts/josefin-sans-v17-latin-regular.woff")
            format("woff"),
        /* Modern Browsers */ url("./fonts/josefin-sans-v17-latin-regular.ttf") format("truetype"),
        /* Safari, Android, iOS */ url("./fonts/josefin-sans-v17-latin-regular.svg#JosefinSans")
            format("svg"); /* Legacy iOS */
}
body {
    margin: 0;
    padding-right: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
        "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

a {
    color: inherit;
    text-decoration: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}
